import React, { useState, useEffect, useRef } from 'react'
import debounce from 'lodash.debounce'
import { send, onDisconnect, onConnect, connect, close } from '../../api'
import Joystick from 'react-nipple'

import PowerButton from '../components/PowerButton'
import WireFrame from '../components/wireFrame'
import styles from './style.module.css'

const PageContainer = () => {
  const [x, setX] = useState(0)
  const [y, setY] = useState(0)
  const firstRun = useRef(3)
  const [powerOn, setPowerOn] = useState(false)
  const [on, setOn] = useState(powerOn)
  const [frameOn, setFrameOn] = useState(false)
  const debouncedFramesCall = debounce(() => setFrameOn(powerOn), 500)
  const debouncedOnCall = debounce(() => setOn(powerOn), 1000, {
    'leading': false,
    'trailing': true
  })
  const instances = useRef([])

  useEffect(() => {
    close()
    connect()

    onConnect(() => {
      console.log('Connected!')
    })
    onDisconnect(() => console.log('Disconnected') | close())
  }, [])

  useEffect(() => sendData(), [x, y])

  useEffect(() => {
    if (firstRun.current > 0) {
      firstRun.current -= 1

      return
    }

    if (on) {
      instances.current.forEach(instance =>  instance[0].show())
    } else {
      instances.current.forEach(instance => instance[0].hide())
    }


  }, [on])

  useEffect(() => {

    if (firstRun.current > 0) {
      firstRun.current -= 1

      return
    }

    debouncedFramesCall()
  }, [powerOn])

  useEffect(() => {
    if (firstRun.current > 0) {
      firstRun.current -= 1

      return
    }

    debouncedOnCall()
  }, [frameOn])

  const sendData = () => {
    console.log('sending data:', {
      x, y
    })
    send({type: 'JOYSTICK',
      payload: {x, y}
    })
  }

  const onCreate = instance => {
    instance[0].hide()
    instances.current.push(instance)
  }

  const onMove = (event, data) => {
    if (!on) {
      return
    }
    const { direction, instance } = data

    if (!direction?.y) { // X-AXIS
      setX(instance.frontPosition.x)
    } else {
      setY(-instance.frontPosition.y)
    }
  }

  const onEnd = (event, data) => {
    if (!on) {
      return
    }
    const { frontPosition } = data
    data.show()

    if (frontPosition.x !== 0) { setX(0) } else { setY(0) }
  }

  return (
    <main className={styles['main']}>
      <PowerButton onClick={() => setPowerOn(!powerOn)}
        state={powerOn} />
      <WireFrame on={frameOn} />
      <div className={!on ? styles['controllers'] : `${styles['controllers']} ${styles['active']}`}>
        <div className={`${styles['leftStick']}`}>
          <Joystick onEnd={onEnd}
            onMove={onMove}
            onCreated={onCreate}
            options={
              { mode: 'static',
                position: {
                  right: '50px',
                  top: '50px'
                },
                lockY: true,
                cacthDistance: 150,
                color: 'white'
              }
            }
            style={{
              // outline: '1px dashed red',
              width: '100px',
              borderRadius: '50%',
              height: '100px'
            // if you pass position: 'relative', you don't need to import the stylesheet
            }} />
          <label className={styles['label']}
            id="y-l">y: {parseInt(y)}</label>
        </div>
        <div className={`${styles['rightStick']}`}>
          <Joystick onEnd={onEnd}
            onMove={onMove}
            onCreated={onCreate}
            options={
              { mode: 'static',
                position: {
                  left: '50px',
                  top: '50px'
                },
                lockX: true,
                cacthDistance: 150
              }
            }
            style={{
              // outline: '1px dashed red',
              width: '100px',
              borderRadius: '50%',
              height: '100px'
            // if you pass position: 'relative', you don't need to import the stylesheet
            }} />
          <label className={styles['label']}
            id="x-l">x: {parseInt(x)}</label>
        </div>
      </div>
    </main>)
}

export default PageContainer
